import React, {useEffect} from 'react';
import './App.css';
import data from './data.json';

import {BrowserRouter as Router, Redirect, Route, Switch} from 'react-router-dom';

import Catalog from "./components/Catalog/Catalog";
import Category from "./components/Catalog/Category/Category";
import WebXR from "./components/WebXR/WebXR";
import Size from "./components/Catalog/Size/Size";
import {initGA} from "./index";
import Video from "./components/Catalog/Video/Video";

function App() {
    useEffect(() => {
        initGA();
    }, []);

    return (
        <Router>
            <Switch>
                <Route exact path='/catalog'>
                    <Catalog data={data}/>
                </Route>
                <Route exact path='/catalog/:category'>
                    <Category data={data}/>
                </Route>
                {/*<Route exact path='/ar/:category/:productId'>
                    <WebXR data={data}/>
                </Route>*/}
                <Route exact path='/size/:category'>
                    <Size data={data}/>
                </Route>
                <Route exact path='/video'>
                    <Video/>
                </Route>
                <Redirect from='/' to='/catalog'/>
            </Switch>
        </Router>
    );
}

export default App;
