import React, {useEffect, useRef} from 'react';
import {useLoader, useThree} from "@react-three/fiber";
import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader";
import {DragControls} from "three/examples/jsm/controls/DragControls";

const ARModel = ({product: {arGLTF}, matrix, onShowOverlay, scale}) => {
    const {camera, gl: {domElement}} = useThree();
    const gltf = useLoader(GLTFLoader, arGLTF);
    const controls = useRef({});
    const axisYPosition = useRef(0);

    useEffect(() => {
        if (scale && matrix) {
            // Позиция матрицы раскладывается поздно!!!!!
            gltf.scene.scale.set(scale.x, scale.y, scale.z);
            gltf.scene.position.setFromMatrixPosition(matrix);
            axisYPosition.current = gltf.scene.position.y;
        }

        /*gltf.scene.scale.set(scale.x, scale.y, scale.z);
        gltf.scene.position.setFromMatrixPosition(matrix);
        axisYPosition.current = gltf.scene.position.y;*/

        controls.current = new DragControls([gltf.scene], camera, domElement);
        controls.current.transformGroup = true;

        const handleDragMove = () => (gltf.scene.position.y = axisYPosition.current);
        const handleDragStart = () => onShowOverlay(false);
        const handleDragEnd = () => onShowOverlay(true);

        controls.current.addEventListener('drag', handleDragMove);
        controls.current.addEventListener('dragstart', handleDragStart);
        controls.current.addEventListener('dragend', handleDragEnd);
    }, []);

    return (
        <primitive object={gltf.scene}/>
    );
}

export default ARModel;