import React, {useRef} from 'react';
import {useHitTest} from "@react-three/xr";
import {Box} from "@react-three/drei";

const ARHitTest = ({onHit, onHitMatrix}) => {
    const box = useRef();

    useHitTest((hit) => {
        if (box.current) {
            hit.decompose(box.current.position, box.current.rotation, box.current.scale);
        }
    });

    const handlePointerDown = () => {
        onHitMatrix(box.current.matrix);
        onHit(true);
    }

    return (
        <Box ref={box} args={[0.12, 0.01, 0.12]} onPointerDown={handlePointerDown}/>
    );
}

export default ARHitTest;