import React, {useEffect, useState} from 'react';
import './Category.css';
import {Link, useParams} from 'react-router-dom';
import {GAevent} from "../../GoogleAnalytics/Events";
import WebXR from "../../WebXR/WebXR";

const Category = ({data}) => {
    const {category} = useParams();
    const [os, setOS] = useState('');
    const [selectProduct, setSelectProduct] = useState(null);

    const handleGAEventClickStartAR = (title) => GAevent('CATEGORY', 'click startAR', title);
    const handleGAEventClickSize = (title) => GAevent('CATEGORY', 'click size', title);

    useEffect(() => {
        const userAgent = window.navigator.userAgent;

        if (/iPhone|iPad|iPod/i.test(userAgent)) {
            setOS('apple')
        } else {
            setOS('android');
        }
    }, []);

    const handleSetProduct = (product) => {
        if (os === 'android') {
            setSelectProduct(product);
        }

        handleGAEventClickStartAR(product.title);
    }

    const handleClickMessage = (eo) => {
        if (eo.data === '_apple_ar_quicklook_button_tapped') {

        }
    }

    const getProduct = (product) => {
        const {id, title, image, color, size, price, usdz} = product;

        if (os === 'apple') {
            return (
                <li key={id} className='category_item'>
                    <a className='category_item_apple_link' id="ar-link" href={usdz} rel='ar' >
                        <div className='category_item_image_container'>
                            <img className='category_item_image' src={image} alt={title}/>
                            <img className='category_item_ar_image' src='/assets/images/other/ar-link.svg' alt='ar'/>
                        </div>
                        <div className='category_item_title'>{title}</div>
                        <div className='category_additional_info_container'>
                            <div className='category_item_color'><span>Цвет: </span>{color}</div>
                            <div className='category_item_size'><span>Размеры, см: </span>{size}</div>
                        </div>
                        <div className='category_item_price'>{price} BYN</div>
                    </a>
                </li>
            )
        } else {
            return (
                <li key={id} className='category_item' onClick={() => handleSetProduct(product)}>
                    <div className='category_item_image_container'>
                        <img className='category_item_image' src={image} alt={title}/>
                        <img className='category_item_ar_image' src='/assets/images/other/ar-link.svg' alt='ar'/>
                    </div>
                    <div className='category_item_title'>{title}</div>
                    <div className='category_additional_info_container'>
                        <div className='category_item_color'><span>Цвет: </span>{color}</div>
                        <div className='category_item_size'><span>Размеры, см: </span>{size}</div>
                    </div>
                    <div className='category_item_price'>{price} BYN</div>
                </li>
            );
        }
    }

    const getCategoryProducts = () => {
        const {products} = data[category];

        return products.map(product => {
            return getProduct(product);
        });
    }

    return (
        <>
            {!selectProduct &&
            <div className='category_container'>
                <div className='category_header'>
                    {data[category].title}
                </div>

                <ul className='category_list'>
                    {getCategoryProducts()}
                </ul>

                {os === 'android' &&
                <Link className='category_link_size_container' to={`/size/${category}`}
                      onClick={() => handleGAEventClickSize(data[category].title)}>
                    <button className='category_link_size' type='button'>
                        Подобрать по размерам
                    </button>
                </Link>
                }

            </div>}

            {selectProduct &&
            <WebXR product={selectProduct} onSetProduct={handleSetProduct} scale={{x: 1, y: 1, z: 1}}/>}
        </>
    );
}

export default Category;