import React, {useRef, useState} from 'react';
import './Size.css';
import {useParams} from "react-router";
import {GAevent} from "../../GoogleAnalytics/Events";
import WebXR from "../../WebXR/WebXR";

const Size = ({data}) => {
    const {category} = useParams();
    const xInput = useRef();
    const yInput = useRef();
    const zInput = useRef();
    const scale = useRef({});
    const [selectProduct, setSelectProduct] = useState(null);

    const handleGAEventClickAcceptButton = () => {
        GAevent(
            'SIZE',
            'click accept button',
            `
            X - ${xInput.current.value ? xInput.current.value : 'default'} // 
            Y - ${yInput.current.value ? yInput.current.value : 'default'} //
            Z - ${zInput.current.value ? zInput.current.value : 'default'}
            `
        );

        setSelectProduct(data[category]);
    }

    const handleClickInput = (eo) => {
        if (eo.target.value === '0') {
            eo.target.value = '';
        }
    }

    const handleBlurInput = (eo) => {
        if (eo.target.value === '') {
            eo.target.value = '0';
        }
    }

    const getInputs = () => {
        return data[category].sides.map((side, index) => {
            const ref = index === 0 ? xInput : index === 1 ? yInput : zInput;
            return (
                <div key={index} className='size_input_item'>
                    <label htmlFor={side[0]}>{side[1]}: </label>
                    <input ref={ref} className='size_input' id={side[0]} type='number' defaultValue='0'
                           onClick={handleClickInput} onBlur={handleBlurInput}
                    />
                    <span>см</span>
                </div>
            )
        });
    }

    const modelScale = () => {
        const def = data[category].grayModel.size;

        if (def[0] && def[1]) {
            const x = Number(xInput.current.value);
            const y = Number(yInput.current.value);
            const baseX = def[0];
            const baseY = def[1];

            scale.current = {
                x: (x === 0) ? 1 : x / baseX,
                y: (y === 0) ? 1 : y / baseY
            }

            if (def[2]) {
                const z = Number(zInput.current.value);
                const baseZ = def[2];
                scale.current = {
                    ...scale.current,
                    z: (z === 0) ? 1 : z / baseZ
                }
            }
        }
    }

    const handleSetProduct = (product) => {
        setSelectProduct(product);
    }

    return (
        <>
            {!selectProduct &&
            <div className='size_container'>
                <div className='size_header'>Введите размеры объекта</div>

                <div className='size_image_container'>
                    <img className='size_image' src={data[category].figure} alt='size'/>
                </div>

                <div className='size_input_list'>
                    {getInputs()}
                </div>

                <div className='size_accept_button_container' onClick={modelScale}>

                    <button
                        className='size_accept_button'
                        type='button'
                        onClick={handleGAEventClickAcceptButton}
                    >
                        Применить
                    </button>
                </div>
            </div>}

            {selectProduct &&
            <WebXR product={data[category].grayModel} onSetProduct={handleSetProduct} scale={scale.current}/>
            }
        </>
    );
}

export default Size;