import React, {useEffect, useState, Suspense, useRef} from 'react';
import './WebXR.css';
import {useLocation} from "react-router";
import {unmountComponentAtNode} from "@react-three/fiber";
import {ARCanvas, DefaultXRControllers} from "@react-three/xr";

import ARModel from "./ARModel/ARModel";
import ARHitTest from "./ARHitTest/ARHitTest";
import AROverlay from "./AROverlay/AROverlay";

import ARLoader from "./ARLoader/ARLoader";
import {ARButton} from "./ARButton";
import AROverlayGray from "./AROverlay/AROverlayGray";
import {GAevent} from "../GoogleAnalytics/Events";

const WebXR = ({product, onSetProduct, scale}) => {
    const location = useLocation();
    const canvas = useRef();
    const time = useRef(Date.now());

    const [isHit, setIsHit] = useState(false);
    const [hitMatrix, setHitMatrix] = useState({});
    const [mode, setMode] = useState('');

    const [showOverlay, setShowOverlay] = useState(true);

    const handleGAEventSessionDuration = (time) => {
        GAevent('AR SESSION', 'duration session', `${time} seconds`);
    }

    useEffect(() => {
        const path = location.pathname.split('/')[1];
        setMode((path) === 'size' ? 'grayModel' : 'model');

        return () => {
            if (document.getElementById('ARButton')) {
                document.body.removeChild(document.getElementById('ARButton'));
            }
            unmountComponentAtNode(document.querySelector('canvas'));
        }
    }, [product]);

    const handleIsHit = (hit) => setIsHit(hit);
    const handleHitMatrix = (matrix) => setHitMatrix(matrix);
    const handleSetShowOverlay = (state) => setShowOverlay(state);

    const handleStartSession = async (gl) => {
        const arConfig = {
            requiredFeatures: ['hit-test'],
            optionalFeatures: ['dom-overlay'],
            domOverlay: {root: document.querySelector('.canvas_container')}
        }

        const btn = await ARButton.createButton(gl, arConfig);
        document.body.append(btn);
        const arButton = await document.getElementById('ARButton');

        setTimeout(() => {
            arButton.click();
        }, 300);

        gl.xr.addEventListener('sessionend', () => {
            const timeEnd = Date.now();
            handleGAEventSessionDuration((timeEnd - time.current) / 1000);
            onSetProduct(null);
        });
    }

    return (
        <div className='canvas_container' ref={canvas}>

            <ARCanvas className='ARCanvas' mode="concurrent" dpr onCreated={({gl}) => handleStartSession(gl)}>
                <directionalLight position={[0, 1, 6]} intensity={1}/>
                <directionalLight position={[0, 1, -6]} intensity={1}/>
                <directionalLight position={[6, 1, 0]} intensity={1}/>
                <directionalLight position={[-6, 2, 0]} intensity={1}/>
                <directionalLight position={[0, -5, 0]} intensity={1}/>

                <Suspense fallback={<ARLoader/>}>
                    {!isHit && <ARHitTest onHit={handleIsHit} onHitMatrix={handleHitMatrix}/>}

                    {isHit &&
                    <ARModel product={product} matrix={hitMatrix} scale={scale} onShowOverlay={handleSetShowOverlay}/>}
                </Suspense>

                <DefaultXRControllers/>
            </ARCanvas>
            {isHit && (mode === 'model') ?
                <AROverlay product={product} onHit={handleIsHit} onShowOverlay={showOverlay}/> :
                isHit && (mode === 'grayModel') ?
                    <AROverlayGray product={product} onHit={handleIsHit} onShowOverlay={showOverlay} scale={scale}/> :
                    null}
        </div>
    )
}

export default WebXR;